"use client";

import { products } from "@/lib/products";
import { ProductCard } from "@/components/product-card";
import { TopStrip } from "@/components/top-strip";
import { FAQSection } from "@/components/faq-section";
import { Instagram, MessageCircle, Globe, ShieldCheck } from "lucide-react";

export default function Home() {
  return (
    <div className="min-h-screen bg-background text-foreground flex flex-col">
      <TopStrip />
      
      <main className="flex flex-col md:flex-row flex-grow">
        {/* Left Sidebar - Profile & Info */}
        <aside className="w-full md:w-1/4 lg:w-1/5 p-6 md:fixed md:top-[48px] md:h-[calc(100vh-48px)] border-r border-border flex flex-col items-center text-center bg-card/50 backdrop-blur-sm z-10 overflow-y-auto">
          <div className="mb-6 mt-10">
            <div className="w-32 h-32 rounded-full bg-gradient-to-br from-neutral-800 to-neutral-600 flex items-center justify-center mb-4 mx-auto shadow-lg border-2 border-neutral-700">
               {/* Placeholder for Profile Pic */}
               <span className="text-4xl">ðŸ‘‘</span>
            </div>
            <h1 className="text-2xl font-bold mb-2">Apex Vendors</h1>
            <div className="flex items-center justify-center gap-1 text-sm text-green-500 mb-4">
               <ShieldCheck size={16} />
               <span className="font-semibold">Verified Reseller</span>
            </div>
            <p className="text-sm text-neutral-500 dark:text-neutral-400 mb-6">
              Access the exact suppliers that verify and test products. 
              High quality, passing serials, and fast shipping.
            </p>
          </div>

          <div className="flex gap-4 mb-8">
            <button className="p-2 rounded-full bg-neutral-200 dark:bg-neutral-800 hover:bg-neutral-300 dark:hover:bg-neutral-700 transition-colors">
              <Instagram size={20} />
            </button>
            <button className="p-2 rounded-full bg-neutral-200 dark:bg-neutral-800 hover:bg-neutral-300 dark:hover:bg-neutral-700 transition-colors">
              <MessageCircle size={20} />
            </button>
            <button className="p-2 rounded-full bg-neutral-200 dark:bg-neutral-800 hover:bg-neutral-300 dark:hover:bg-neutral-700 transition-colors">
              <Globe size={20} />
            </button>
          </div>

          <div className="mt-auto mb-4 text-xs text-neutral-500">
            &copy; {new Date().getFullYear()} Apex Vendors
          </div>
        </aside>

        {/* Right Content - Product Grid */}
        <div className="flex-1 md:ml-[25%] lg:ml-[20%] p-4 md:p-10 overflow-hidden">
          <div className="max-w-6xl mx-auto">
            <header className="mb-8 flex justify-between items-center px-2">
              <h2 className="text-xl font-semibold">Available Vendors</h2>
              <div className="text-sm text-neutral-500">
                {products.length} Products
              </div>
            </header>

            {/* Mobile: Horizontal Scroll | Desktop: Grid */}
            <div className="flex overflow-x-auto snap-x-mandatory gap-4 pb-6 md:grid md:grid-cols-2 lg:grid-cols-3 md:gap-6 md:overflow-visible scrollbar-hide">
              {products.map((product) => (
                <div key={product.id} className="snap-center min-w-[85vw] sm:min-w-[300px] md:min-w-0 md:w-auto flex-shrink-0">
                  <ProductCard product={product} />
                </div>
              ))}
            </div>
            
            <div className="px-2">
               <FAQSection />
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}
